import { Hono } from "hono";
import { PrismaClient } from '@prisma/client/edge'
import { withAccelerate } from '@prisma/extension-accelerate'
import {decode, sign, verify} from 'hono/jwt'


export const blogRouter = new Hono<{
    Bindings:{
        DATABASE_URL: string,
        JWT_SECRET: string
    },
	Variables:{
		userId: string
	}
}>();


blogRouter.use('/*', async (c, next) => {
	const header = c.req.header("authorization") || ""
	if(!header){
		c.status(401);
		return c.json({err:"unauthorized"})
	}
	const token = header.split(" ")[1]
	const response = await verify(token, c.env.JWT_SECRET)
	if(!response){
		c.status(403)
		return c.json({error:"unauthorized"})
	}
	c.set("userId", response.id)
	await next()
})


//todo -> add pagination!
// what is pagination?
// instead of getting all the blogs, we should get as and when the user scrolls thus making the website optimized
blogRouter.get('/bulk',async (c) => {
	const prisma = new PrismaClient({
        datasourceUrl: c.env.DATABASE_URL,
    }).$extends(withAccelerate())

	try{

		const blogs = await prisma.blog.findMany()
		return c.json({
			blogs
		})
	}catch(err){
		c.status(411);
		return c.json({
			err:"Error while fetching all blogs"
		})
	}
})

blogRouter.get('/:id', async (c) => {
	const prisma = new PrismaClient({
        datasourceUrl: c.env.DATABASE_URL,
    }).$extends(withAccelerate())
    const id =  c.req.param();

	try{

		const blog = await prisma.blog.findFirst({
			where:{
				id: id
			}
		})
		return c.json({
			blog
		})
	}catch(err){
		c.status(411);
		return c.json({
			err:"Error while fetching blog"
		})
	}
})

blogRouter.post('/',async (c) => {
    const prisma = new PrismaClient({
        datasourceUrl: c.env.DATABASE_URL,
    }).$extends(withAccelerate())
    const body = await c.req.json();
	const userId = c.get("userId")
	try{
		const blog = await prisma.blog.create({
			data:{
				title: body.title,
				content: body.content,
				authorId: userId
			}
		})
		return c.json({
			id: blog.id
		})
	}catch(err){
		c.status(411);
		c.json({
			err:"Error while posting blog"
		})
	}
})

blogRouter.put('/', async (c) => {
	const prisma = new PrismaClient({
        datasourceUrl: c.env.DATABASE_URL,
    }).$extends(withAccelerate())
    const body = await c.req.json();
	try{
		const blog = await prisma.blog.update({
			where:{
				id: body.id
			},
			data:{
				title: body.title,
				content: body.content
			}
		})
		return c.json({
			id: blog.id
		})
	}catch(err){
		c.status(411);
		c.json({
			err:"Error while updating blog"
		})
	}
})